import { Component, OnInit } from '@angular/core';

const texts = ['Desarrollador de Software'];
let textsIndex = 0;
let index = 0;

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html'
})
export class HeaderComponent implements OnInit {
  smallTextTyped: string;

  constructor() {
    this.smallTextTyped = '';
  }

  ngOnInit(): void {
    this.typing();
    window.addEventListener('scroll', (event) => {
      console.log(window.scrollY);
      if (window.scrollY > 450) {
        document.querySelector('.navbar').classList.add('is-not-transparent');
      }else{
        document.querySelector('.navbar').classList.remove('is-not-transparent');
      }
    });
  }

  typing(): void {

    if (index < texts[textsIndex].length) {
      this.smallTextTyped += texts[textsIndex].charAt(index);
      index++;
      setTimeout(() => { this.typing(); }, 200);
    } else {
      setTimeout(() => { this.erase(); }, 2000);
    }
  }

  erase(): void {
    if (index > 0) {
      this.smallTextTyped = texts[textsIndex].substring(0, index - 1);
      index--;
      setTimeout(() => { this.erase(); }, 100);
    } else {
      textsIndex++;
      if (textsIndex >= texts.length) { textsIndex = 0; }
      setTimeout(() => { this.typing(); }, 1300);
    }
  }

  scrollToElement(selector): void {
    const item = document.querySelector(selector);
    item.scrollIntoView({ behavior: "smooth", block: "start", inline: "nearest" });
  }
}
